import React from 'react';
import './style.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

const Testimonials = () => {
    var settings = {
        loop: true,
        autoplay: true,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
    return (
        <div>
            <Slider {...settings}>
                <div className='testi_card'>
                    <img src="./images/sli1.jpg" className='w-100' alt="" />
                </div>
                <div className='testi_card'>
                    <img src="./images/sli2.jpg" className='w-100' alt="" />
                </div>
                <div className='testi_card'>
                    <img src="./images/sli3.jpg" className='w-100' alt="" />
                </div>
            </Slider>
        </div>
    )
}

export default Testimonials