import React from 'react'
import { Menu } from 'antd';
const { SubMenu } = Menu;


function getItem(label, key, children, type) {
    return {
        key,
        children,
        label,
        type,
    };
}

const items = [
    getItem('Home', 'sub4ba', null),
    getItem('About Us', 'sub2', [
        getItem('Introduction', 'sub3',
            [getItem('BMG values and guiding principles', '2'),
            getItem('BMG Vision, Mission and Goals', '3'),
            getItem('BMG Objectives', '4')]),
        getItem('BMG Leadership', 'sub4',
            [getItem('Board Of Directors', '5'),
            getItem('Advisor board', '6'),
            getItem('Senior Leadership', '8'),
            getItem('Secretariat', '10')]),
        getItem('BMG Policies', '11'),
        getItem('BMG Technical Capacity (in-house expertise)', '12')
    ]),
    getItem('BMG Program', 'sub2a', [
        getItem('Current Programs', 'sub3a',
            [getItem('Media Advocacy', '2a'),
            getItem('TV Program', '3a'),
            getItem('Local Government Strengthening', '4a'),
            getItem('TVC', '5a'),
            getItem('Documentary', '6a'),
            getItem('National/ Provincial Summit', '7a'),
            getItem('TVC Project', '8a'),
            getItem('Media Consulting Service', '9a')]),
        getItem('BMG Strategic Priority 2022-2024', '10a'),
        getItem('Strategies (Strategic plan for 2022-2024)', '11a'),
        getItem('BMG community initiatives)', '12a')
    ]),
    getItem('Resource and Media', 'sub4b', [
        getItem('Press Release', '1b'),
        getItem('News coverage', '2b'),
        getItem('Speech/Videos', '3b'),
        getItem('Gallery', '4b'),
        getItem('Annual Report', '5b'),
        getItem('Published Document', '6b'),
    ]),
    getItem('Get Involved', 'sub5', [
        getItem('Partnership', 'sub3c',
            [getItem('Government (Federal, Provincial, Local govt)', '2c'),
            getItem('Donor/ collaborators', '3c'),
            getItem('Private', '4c')]),
        getItem('Career', '11c'),
        getItem('Internship', '12c')
    ]),
];

const Header = () => {

    const onClick = (e) => {
        console.log('click ', e);
    };

    return (
        <div className='navigation'>
            <nav className="navbar navbar-expand-lg">
                <div className="container-fluid">
                    <img src="./images/logo.png" height={70} width={200} alt="" />
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon" />
                    </button>
                    <div className="col-lg-1"></div>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav lap-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <a className="nav-link active" aria-current="page" href="#">Home</a>
                            </li>
                            <li className='about nav-item'>
                                About Us
                                <ul className='sub-about'>
                                    <li className='sub-child pt-3 mt-2'><span>Intorduction</span>
                                        <ul className='sub-hover'>
                                            <li className='pt-3'><span>BMG values and guiding principles</span></li><hr />
                                            <li><span>BMG Vision, Mission and Goals</span></li><hr />
                                            <li className='mb-3'><span>BMG Objectives</span></li>
                                        </ul>
                                    </li><hr />
                                    <li className='sub-child'><span>BMG Leadership</span>
                                        <ul className='sub-hover' style={{ marginTop: '-121px' }}>
                                            <li className='pt-3'><span>Board of Directors</span></li><hr />
                                            <li><span>Advisor Board</span></li><hr />
                                            <li><span>Senior Leadership</span></li><hr />
                                            <li className='mb-3'><span>Secretariat</span></li>
                                        </ul>
                                    </li><hr />
                                    <li className='sub-child'><span>BMG Policeis</span></li><hr />
                                    <li className='sub-child pb-3'><span>BMG Technical Capacity <br />in-house experties</span></li>
                                </ul>
                            </li>
                            <li className='about nav-item'>
                                BMG Programs
                                <ul className='sub-about'>
                                    <li className='sub-child pt-3 mt-2'><span>Current Programs</span>
                                        <ul className='sub-hover'>
                                            <li className='pt-3'><span>Media Advocacy</span></li><hr />
                                            <li><span>TV Program</span></li><hr />
                                            <li><span>TVC</span></li><hr />
                                            <li><span>Documentary</span></li><hr />
                                            <li><span>National \Provincial Summit</span></li><hr />
                                            {/* <li><span>TVC Project</span></li><hr /> */}
                                            <li><span>Local Government Strengthening</span></li><hr />
                                            <li className='mb-3'><span>Media Consulting Service</span></li>
                                        </ul>
                                    </li><hr />

                                    <li className='sub-child'><span>BMG Strategic Priority 2022-2024</span></li><hr />
                                    <li className='sub-child'><span>Strategies (Strategic plan for 2022-2024)</span></li><hr />
                                    <li className='sub-child pb-3'><span>BMG Community Initiatives</span></li>
                                </ul>
                            </li>
                            <li className='about nav-item'>
                                Resource and Media
                                <ul className='sub-about' style={{ position: 'absolute', right: '-35px' }}>
                                    <li className='sub-child pt-3'><span>Press Realese</span></li><hr />
                                    <li className='sub-child'><span>News Coverage</span></li><hr />
                                    <li className='sub-child'><span>Speech/Videos</span></li><hr />
                                    <li className='sub-child'><span>Gallery</span></li><hr />
                                    <li className='sub-child'><span>Annual Report</span></li><hr />
                                    <li className='sub-child pb-3'><span>Published Document</span></li>
                                </ul>
                            </li>
                            <li className='about nav-item'>
                                Get Involved
                                <ul className='sub-about'>
                                    <li className="nav-item dropdown">
                                        <a className="nav-link mt-3 dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                            <span>Partnership</span>
                                        </a>
                                        <ul style={{ marginLeft: '-23px' }} className="dropdown-menu" aria-labelledby="navbarDropdown">
                                            <li><a className="dropdown-item" href="#">Government</a></li>
                                            <li><a className="dropdown-item" href="#">Donor/Collaborators</a></li>
                                            <li><a className="dropdown-item" href="#">Private</a></li>
                                        </ul>
                                    </li>
                                    <hr />
                                    <li className='sub-child'><span>Career</span></li><hr />
                                    <li className='sub-child pb-3'><span>Internship</span></li>
                                </ul>
                            </li>
                        </ul>
                        <div className="mbl-navs mt-4">
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                                <Menu
                                    onClick={onClick}
                                    style={{
                                        width: 350,
                                    }}
                                    defaultSelectedKeys={['1']}
                                    defaultOpenKeys={['sub1']}
                                    mode="inline"
                                    items={items}
                                />
                            </ul>
                        </div>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Header